<template>
  <v-container>
    <div v-if="currentDept" class="edit-form py-3">
      <v-row>
        <v-col class="text-center" cols="12" md="4">
          <v-row class="pa-5">
            <h2>EDIT DEPARTMENT</h2>
          </v-row>
        </v-col>

        <v-col cols="12" md="4">
          <v-spacer />
          <v-btn
            to="/addDept"
            class="text-decoration-none"
            color="primary"
            dark
            block
            small
            >ADD DEPARTMENT</v-btn
          >
        </v-col>

        <v-col cols="12" md="4">
          <v-spacer />
          <v-btn
            to="/depts"
            class="text-decoration-none"
            color="primary"
            dark
            block
            small
            >DEPARTMENT LIST</v-btn
          >
        </v-col>
      </v-row>

      <v-form ref="form" lazy-validation>
        <v-text-field
          v-model="currentDept.name"
          :rules="[(v) => !!v || 'Name is required']"
          label="Name"
          required
        ></v-text-field>

        <v-text-field
          v-model="currentDept.description"
          :rules="[(v) => !!v || 'Description is required']"
          label="Description"
          required
        ></v-text-field>

        <v-btn color="error" small class="mr-2" @click="deleteDept">
          Delete
        </v-btn>

        <v-btn color="success" small @click="updateDept"> Update </v-btn>
      </v-form>

      <p class="mt-3">{{ message }}</p>
    </div>

    <div v-else>
      <p>Please click on a User...</p>
    </div>
  </v-container>
</template>

<script>
import DeptDataService from "../../services/DeptDataService";

export default {
  name: "dept",
  data() {
    return {
      currentDept: null,
      message: "",
    };
  },
  methods: {
    getDept(id) {
      DeptDataService.get(id)
        .then((response) => {
          this.currentDept = response.data;
          // console.log(response.data);
        })
        .catch((e) => {
          console.log(e);
        });
    },

    updateDept() {
      DeptDataService.update(this.currentDept.id, this.currentDept)
        .then((response) => {
          // console.log(response.data);
          this.$store.dispatch("setSnackbar", {
            showing: true,
            text:
              (response && response.data && response.data.message) ||
              toString(),
          });
          this.$router.push({ name: "depts" });
          // this.message = "The Department was updated successfully!";
        })
        .catch((e) => {
          console.log(e);
        });
    },

    deleteDept() {
      DeptDataService.delete(this.currentDept.id)
        .then((response) => {
          // console.log(response.data);
          this.$store.dispatch("setSnackbar", {
            showing: true,
            text:
              (response && response.data && response.data.message) ||
              toString(),
          });
          this.$store.dispatch("setSnackbar", {
            showing: true,
            text:
              (response && response.data && response.data.message) ||
              toString(),
          });
          this.$router.push({ name: "depts" });
        })
        .catch((e) => {
          console.log(e);
        });
    },
  },
  mounted() {
    this.message = "";
    this.getDept(this.$route.params.id);
  },
};
</script>
